/* for medium devices */
@media (max-width: 768px) {
    .login-box {
        width: 80%;
    }

    .side-menu-toggle {
        display: block;
        
    }

    .side-pane {
        position: fixed;
        top: 0;
        left: -300px;
        height: 100vh;
        width: 100%;
        max-width: 300px;
        transition: 0.2s linear;
    }

    .tab-content {
        background-color: #E5FDD1;
        
    }

    .carousel-item img {
        height: 30px;
        max-width: 50%;
      }
    
}

@media (min-width: 992px) {
    .carousel-item img {
      height: 300px;
      max-width: 30%;
    }
  }
  
  @media (min-width: 1200px) {
