/* WORKSHOP */

.app {
    display: flex;
    min-height: 100vh;
}

.admin-side-bar {
    flex: 1 1 0;
    max-width: 300px;
    padding: 2rem 1rem;
    background-color: #2e3047;
}

.admin-side-bar h4 {
    color: #707793;
    font-size: 0.75rem;
    text-transform: uppercase;
    margin-bottom: 0.5em;
}

.admin-side-bar .admin-menu {
    margin: 0 -1rem;
}

.admin-side-bar .admin-menu .menu-item {
    display: block;
    padding: 1em;
    color: #FFF;
    text-decoration: none;
    transition: 0.2s linear;
}

.admin-side-bar .admin-menu .menu-item:hover,
.admin-side-bar .admin-menu .menu-item.is-active {
    color: #3bba9c;
    border-right: 5px solid #3bba9c;

}

.admin-content {
    flex: 1 1 0;
    padding: 2rem;
}

.admin-content h1 {
    color: #3c3f58;
    font-size: 2.5rem;
    margin-bottom: 1rem;
}

.menu-toggle {
    display: none;
    position: fixed;
    top: 2rem;
    right: 2rem;
    width: 60px;
    height: 60px;
    border-radius: 99px;
    background-color: #2e3047;
    cursor: pointer;
}

.hamburger {
    position: relative;
    top: (50% - 2px);
    left: 50%;
    transform: translate(-50%, -50%);
    width: 32px;
}

.hamburger > span,
.hamburger > span::before,
.hamburger > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 4px;
    border-radius: 99px;
    background-color: #FFF;
    transition-duration: .25s;
}

.hamburger > span::before {
    content: '';
    top: -8px;
}

.hamburger > span::after {
    content: '';
    top: 8px;
}

.menu-toggle.is-active .hamburger > span {
    transform: rotate(45deg);
  }
  .menu-toggle.is-active .hamburger > span::before {
    top: 0;
    transform: rotate(0deg);
  }
  .menu-toggle.is-active .hamburger > span::after {
    top: 0;
    transform: rotate(90deg);
  }


@media (max-width: 1024px) {
    .admin-side-bar {
        max-width: 200px;
    }
}

@media (max-width: 768px) {
    .menu-toggle {
        display: block;
    }

    .admin-content {
        padding-top: 8rem;
    }

    .admin-side-bar {
        position: fixed;
        top: 0;
        left: -300px;
        height: 100vh;
        width: 100%;
        max-width: 300px;
        transition: 0.2s linear;
    }

    .admin-side-bar.is-active {
        left: 0;
    }
}